.footer {
  background-color: $tangaroa;
  padding-top: 50px;
  padding-bottom: 43px;

  @media (max-width: $tablet-width) {
    padding-top: 42px;
    padding-bottom: 40px;
  }

  @media (max-width: $mobile-width) {
    padding-top: 30px;
    padding-bottom: 40px;
  }
}

.footer__container {
  display: flex;

  @media (max-width: $mobile-width) {
    display: block;
  }
}

.footer__logo {
  margin-right: 40px;

  svg {
    width: 70px;
    height: 38px;
    fill: $base-white;
  }

  @media (max-width: $tablet-width) {
    margin-top: 10px;
    margin-right: 25px;

    svg {
      width: 38px;
      height: 20px;
    }
  }

  @media (max-width: $mobile-width) {
    svg {
      width: 42px;
      height: 22px;
    }

    margin-bottom: 15px;
  }

  &:hover svg {
    fill: $deep-sky-blue;
  }
}

.footer__navigation {
  margin-right: auto;

  ul {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    margin-top: 6px;
    padding: 0;
    gap: 33px;
    max-width: 800px;
    margin-right: 40px;

    @media (max-width: 1300px) {
      max-width: 580px;
    }

    @media (max-width: $tablet-width) {
      gap: 23px;
      margin-top: 5px;
      max-width: 500px;
    }

    @media (max-width: $mobile-width) {
      display: block;
    }
  }

  li {
    @media (max-width: $mobile-width) {
      margin-bottom: 8px;

      &:last-of-type {
        margin-bottom: 22px;
      }
    }
  }

  a {
    cursor: pointer;
    color: $base-white;
    font-size: 16px;
    line-height: 20px;
    font-weight: 500;

    @media (max-width: $tablet-width) {
      font-size: 12px;
      line-height: 15px;
    }

    &:hover,
    &:focus {
      padding-bottom: 2px;
      border-bottom: 1px solid $base-white;
    }

    &:active {
      color: $danube;
    }
  }
}

.footer__social {
  ul {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    margin-top: 11px;
    padding: 0;
    gap: 27px;
    min-width: 108px;
    max-width: 300px;
    margin-left: 0;

    @media (max-width: $tablet-width) {
      min-width: 82px;
      margin-left: 0;
      gap: 21px;
    }

    @media (max-width: $mobile-width) {
      margin-top: 0;
      margin-left: 0;
      gap: 21px;
    }
  }

  a {
    margin: 0;

    svg {
      display: block;
      fill: $base-white;
      width: 20px;
      height: 20px;

      @media (max-width: $tablet-width) {
        width: 17px;
        height: 16px;
      }
    }

    &:hover svg,
    &:focus svg {
      fill: $danube;
    }

    &:active svg {
      fill: $deep-sky-blue;
    }
  }
}

.footer__ok {
  max-width: 12px;

  @media (max-width: $tablet-width) {
    max-width: 10px;
  }
}
